<template>
  <div class="wrapper" ref="wrapper">
    <div class="content">
      <search></search>
    <slider></slider>
    <rollnotice></rollnotice>
    <myplaygamed></myplaygamed>
    <div class="btns">
     <yd-button size="small" type="primary" @click.native="toNewGame">新游</yd-button>
     <yd-button type="danger">活动</yd-button>
     <yd-button type="warning">礼包</yd-button>
    </div>
    <todaygame></todaygame>
    <hotgame></hotgame>
    <hottype></hottype>
    <gametop></gametop>
    </div>
    
  </div>
</template>

<script>
import search from '@/components/Search/Search.vue'
import slider from '@/components/Slider/Slider.vue'
import rollnotice from '@/components/RollNotice/RollNotice.vue'
import myplaygamed from '@/components/MyPlayGamed/MyPlayGamed.vue'
import todaygame from '@/components/TodayGame/TodayGame.vue'
import hotgame from '@/components/HotGame/HotGame.vue'
import hottype from '@/components/HotType/HotType.vue'
import gametop from '@/components/GameTop/GameTop.vue'
import BScroll from 'better-scroll';
export default {
  name: 'HelloWorld',
  data() {
    return {
      data: '我是主页传来的数据'
    }
  },
  props: {
    msg: String
  },
  components: {
    search,
    slider,
    rollnotice,
    myplaygamed,
    todaygame,
    hotgame,
    hottype,
    gametop
  },  mounted() {
    this.$nextTick(() => {
      //$refs绑定元素
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.wrapper, {
          //开启点击事件 默认为false
          click: true
        });
        // console.log(this.scroll)
      } else if (!this.$refs.wrapper) {
        return;
      } else {
        this.scroll.refresh();
      }
    });
  },
  methods: {
    toNewGame() {
      console.log('点击了')
       this.$router.push({  //核心语句
        path:'/newgame',   //跳转的路径
        query:{           //路由传参时push和query搭配使用 ，作用时传递参数
          data:this.data ,  
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus">
.wrapper
  width: 100%;
  position: absolute;
  top: 1px;
  bottom: 50px; /* 关键 */
  overflow: hidden;
  height 100%
  .btns
    display  flex
    justify-content space-between
    margin 10px
</style>
