<!-- 选择就诊人/选择就诊人提示/添加就诊人前后/选中就诊人/预约提交成功 -->
<template>
  <div class="reserve-info">
    <div class="select-person">
      <div class="person-wrap" ref="personWrap">
        <ul class="person-list" ref="personTab">
          <li class="person-item">1</li>
          <li class="person-item">2</li>
          <li class="person-item">3</li>
          <li class="person-item">4</li>
          <li class="person-item">5</li>
          <li class="person-item">6</li>
          <li class="person-item">7</li>
          <li class="person-item">8</li>
          <li class="person-item">9</li>
         
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";
export default {
  name: "ReserveInfo",
  data() {
    return {};
  },
  created() {
    this.$nextTick(() => {
      this.personScroll();
    });
  },
  components: {},
  methods: {
    // 选择就诊人横向滚动
    personScroll() {
      // 默认有六个就诊人
      let width = 4 * 150;
      this.$refs.personTab.style.width = width + "px";
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.personWrap, {
            startX: 0,
            click: true,
            scrollX: true,
            scrollY: false,
            eventPassthrough: "vertical"
          });
        } else {
          this.scroll.refresh();
        }
      });
    }
  }
};
</script>
<style lang='stylus' scoped>
.reserve-info 
  background-color: rgb(250, 250, 250);
  min-height: 30vh;
  .select-person 
    padding: 10px 20px;
    .person-wrap 
      .person-list 
        .person-item 
          display: inline-block;
          height: 50px;
          width: 150px;
          margin-right: 10px;
          border: 1px solid #ccc;
          
        
      
    
  

</style>